#sidebar-graph {
    position:fixed;
    top:70px;
    right:0;
    background: #FFFFFF none repeat scroll 0% 0%;
    color:#0e7f3c;
    border-style: solid;
    border-color: black;
    border-width:0px 0px 1px 1px;
    border-radius:0px 0px 0px 8px;
    font-size: 1rem;
}
#sidebar-graph a {
    color:#0e7f3c;
    margin-right: 100px;
}

#menu-sidebar {
    background-color: #01040d;
    color: #fff;
    display: block;
    height: 100vh;
    left: -390px;
    opacity: 0;
    overflow: auto;
    position: fixed;
    text-align: left;
    top: 0;
    transition: all .25s ease;
    width: 390px;
    z-index: 20000;
}
#menu-sidebar.menu-sidebar-opened {
    opacity: 1;
    left: 0;
    transition: all .5s ease;
}
ul.pagination {
    justify-content: center;
}
ul.pagination li {
    padding: 0.2rem;
}
.graph_svg {
    position:relative;
}
.container_svg_sentence{
    position:relative;
    overflow-x: scroll;
    padding-bottom: 20px;
}
.container_svg{
    position:relative;
}
#elements_html {
    display:none;
}
#myMarkerrelation_2_1{
    cursor: move;
}
select.select_relations{
    font-size: 12px;
}
