path.default {
    stroke-width: 0.75;
    stroke: #888;
}
text.default {
    fill: black;
}
path.disabled {
    stroke-width: 0.75;
    stroke: #888;
}
text.disabled {
    font-weight: normal;
    fill: #888;
}
path.alert, path.danger {
   /* stroke-width: 1;*/
    stroke: #d43f3a;
}
text.alert,text.danger {
    fill: #d9534f;
}

path.alert, path.danger {
   /* stroke-width: 1;*/
    stroke: red;
}
text.alert,text.danger {
    fill: red;
}
path.warning {
    stroke-width: 1;
    stroke: #eea236;
}
text.warning {
    fill: #f0ad4e;
}
path.primary {
    stroke-width: 1;
    stroke: #0070C0;
}
text.primary {
    fill: #0070C0;
}
path.info {
    stroke-width: 1;
    stroke: #46b8da;
}
text.info {
    fill: #5bc0de;
}
path.success {
    stroke-width: 1;
    stroke: #00B050;
}
text.success {
    fill: #00B050;
}
path.hl-alert, path.hl-danger {
   /* stroke-width: 1;*/
    stroke: #d43f3a;
}
text.hl-alert,text.hl-danger {
    fill: #d9534f;
}

path.hl-alert, path.hl-danger {
   /* stroke-width: 1;*/
    stroke: red;
}
text.hl-alert,text.hl-danger {
    fill: red;
}
path.hl-warning {
    stroke-width: 1;
    stroke: #eea236;
}
text.hl-warning {
    fill: #f0ad4e;
}
path.hl-primary {
    stroke-width: 1;
    stroke: #0070C0;
}
text.hl-primary {
    fill: #0070C0;
}
path.hl-info {
    stroke-width: 1;
    stroke: #46b8da;
}
text.hl-info {
    fill: #5bc0de;
}
path.hl-success {
    stroke-width: 1;
    stroke: #00B050;
}
text.hl-success {
    fill: #00B050;
}
path.unhighlight, path.unselected {
    stroke-width: 0.75;
    stroke: #888;
}
text.unhighlight, text.unselected {
    fill: #888;
}
path.highlight, path.selected{
    stroke-width: 1.5;
}
text.highlight, text.selected {
    font-weight: 1.1em;
    fill: blue;
}
text.selected {
    text-decoration: underline;
}
svg.graph_svg_view > text.label_relation {
    cursor : copy;
}
svg.graph_svg_edit > use, svg.graph_svg_edit > rect {
    cursor : move;
}